import React from "react";
const apiposter = "https://image.tmdb.org/t/p/w1280";

const Result = ({ poster_path, title }) => {
  return (
    <div className="result">
      <img src={apiposter + poster_path} alt={title} />
      <h3>{title}</h3>
    </div>
  );
};

export default Result;
